
.icon{
  cursor: pointer;
  font-size: 28px;
}
.scrollable{
  overflow: scroll;
  position: relative;
  height: 64vh;
}
.opend{
  top: 0 !important;
  -webkit-transition: top 0.5s ease !important;
  transition: top 0.5s ease !important;
}
.closed{
  top: 200vw !important;
  -webkit-transition: top 1s ease !important;
  transition: top 1s ease !important;
}
#popupWrapper {
  position: relative;
  height: 80vh;
  width: 97vw;
  overflow: hidden;
  -ms-flex-item-align: center;
      align-self: center;
  background-color: white;
  margin: auto;
  border: 8px solid white;
  -webkit-transition: all 2.5s ease;
  transition: all 2.5s ease;
}
#popupWrapper .topBar {
  color: #403427;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  
  padding: 10px;
  /* On top of popupScroll */
  z-index: 20;
  position: relative;
}
#popupWrapper .topBar .close {
  cursor: pointer;
  font-size: 250%;
  padding: 0 15px;
  text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
#popupWrapper .topBar .close:hover {
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}

/* SMALL SCREENS */
@media only screen and (max-width: 992px){
.scrollable {
      height: 60vh !important;
}
#popupWrapper{
    min-width: 97vw !important;
}
}
@media only screen and (max-width: 768px){
.scrollable {
      height: 57vh !important;
}
}
@media only screen and (max-width: 650px){
.scrollable {
      height: 70vh !important;
}
}

/* TABLETS AND MOBILE DEVICES */
@media only screen and (max-device-width: 768px) {
#popupWrapper{
    position: absolute;
    height: 102vh !important;
    width: 100vw !important;
    top: 0;
    left: 0;
    z-index: 1050;
}
#popupWrapper .topBar {
    padding: 10px !important;
}
#popupWrapper .popup .text {
    width: 100%;
}
    
  /* When popup is hidden  */
#popupWrapper.hidden{
    top: 100vh;
}
}




